'use client'

import { useState } from 'react'

interface AddProjectModalProps {
  isOpen: boolean
  onClose: () => void
}

export default function AddProjectModal({ isOpen, onClose }: AddProjectModalProps) {
  const [name, setName] = useState('')
  const [goal, setGoal] = useState('')
  const [dueDate, setDueDate] = useState('')
  const [mode, setMode] = useState<'solo' | 'collab'>('solo')
  const [repoOption, setRepoOption] = useState<'new' | 'existing'>('new')
  const [existingRepoUrl, setExistingRepoUrl] = useState('')

  const handleSubmit = async () => {
    const payload = {
      name,
      goal,
      dueDate,
      mode,
      teamMembers: [],
      repoOption,
      existingRepoUrl
    }

    try {
      const response = await fetch('http://localhost:8000/api/projects', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      })
      
      const data = await response.json()
      console.log('Response:', data)
      alert(`Project created! ID: ${data.projectId}`)
      
      setName('')
      setGoal('')
      setDueDate('')
      setMode('solo')
      setRepoOption('new')
      setExistingRepoUrl('')
      onClose()
      
    } catch (error) {
      console.error('Error:', error)
      alert('Failed to create project')
    }
  }

  if (!isOpen) return null

  return (
    <>
      <div className="fixed inset-0 bg-black/70 z-40" onClick={onClose} />
      
      <div className="fixed inset-0 z-50 flex items-center justify-center p-4">
        <div className="bg-gray-900 rounded-lg w-full max-w-md p-6 space-y-4 border border-gray-700">
          
          <h2 className="text-xl font-bold text-white">New Project</h2>
          
          <div>
            <label className="block text-sm mb-1 text-gray-300">Name *</label>
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="w-full bg-gray-800 text-white border border-gray-600 px-3 py-2 rounded focus:outline-none focus:border-blue-500"
              placeholder="My Project"
            />
          </div>

          <div>
            <label className="block text-sm mb-1 text-gray-300">Goal *</label>
            <textarea
              value={goal}
              onChange={(e) => setGoal(e.target.value)}
              className="w-full bg-gray-800 text-white border border-gray-600 px-3 py-2 rounded focus:outline-none focus:border-blue-500"
              rows={2}
              placeholder="What are you building?"
            />
          </div>

          <div>
            <label className="block text-sm mb-1 text-gray-300">Due Date *</label>
            <input
              type="date"
              value={dueDate}
              onChange={(e) => setDueDate(e.target.value)}
              className="w-full bg-gray-800 text-white border border-gray-600 px-3 py-2 rounded focus:outline-none focus:border-blue-500"
            />
          </div>

          <div>
  <label className="block text-sm mb-1 text-gray-300">Mode</label>
  <div className="flex gap-2">
    <button
      onClick={() => setMode('solo')}
      style={{
        backgroundColor: mode === 'solo' ? '#16a34a' : '#374151',
        color: 'white',
        padding: '0.5rem 1rem',
        borderRadius: '0.375rem',
        border: mode === 'solo' ? 'none' : '1px solid #4b5563'
      }}
    >
      Solo
    </button>
    <button
      onClick={() => setMode('collab')}
      style={{
        backgroundColor: mode === 'collab' ? '#16a34a' : '#374151',
        color: 'white',
        padding: '0.5rem 1rem',
        borderRadius: '0.375rem',
        border: mode === 'collab' ? 'none' : '1px solid #4b5563'
      }}
    >
      Team
    </button>
  </div>
</div>

<div>
  <label className="block text-sm mb-1 text-gray-300">Repository</label>
  <div className="space-y-2">
    <button
      onClick={() => setRepoOption('new')}
      style={{
        backgroundColor: repoOption === 'new' ? '#16a34a' : '#374151',
        color: 'white',
        padding: '0.5rem 1rem',
        borderRadius: '0.375rem',
        border: repoOption === 'new' ? 'none' : '1px solid #4b5563',
        width: '100%',
        textAlign: 'left'
      }}
    >
      Create New
    </button>
    <button
      onClick={() => setRepoOption('existing')}
      style={{
        backgroundColor: repoOption === 'existing' ? '#16a34a' : '#374151',
        color: 'white',
        padding: '0.5rem 1rem',
        borderRadius: '0.375rem',
        border: repoOption === 'existing' ? 'none' : '1px solid #4b5563',
        width: '100%',
        textAlign: 'left'
      }}
    >
      Link Existing
    </button>
  </div>
</div>

          <div>
            <label className="block text-sm mb-1 text-gray-300">Repository</label>
            <div className="space-y-2">
              <button
                onClick={() => setRepoOption('new')}
                className={`w-full px-4 py-2 rounded ${repoOption === 'new' ? 'bg-green-600 text-white' : 'bg-gray-800 text-gray-300 border border-gray-600'}`}
              >
                Create New
              </button>
              <button
                onClick={() => setRepoOption('existing')}
                className={`w-full px-4 py-2 rounded ${repoOption === 'existing' ? 'bg-green-600 text-white' : 'bg-gray-800 text-gray-300 border border-gray-600'}`}
              >
                Link Existing
              </button>
            </div>
          </div>

          {repoOption === 'existing' && (
            <div>
              <label className="block text-sm mb-1 text-gray-300">Repo URL</label>
              <input
                type="url"
                value={existingRepoUrl}
                onChange={(e) => setExistingRepoUrl(e.target.value)}
                className="w-full bg-gray-800 text-white border border-gray-600 px-3 py-2 rounded focus:outline-none focus:border-blue-500"
                placeholder="https://github.com/user/repo"
              />
            </div>
          )}

          <div className="flex gap-2 pt-2">
            <button
              onClick={handleSubmit}
              disabled={!name || !goal || !dueDate}
              className="flex-1 bg-blue-600 text-white py-2 rounded disabled:bg-gray-600 disabled:cursor-not-allowed hover:bg-blue-700"
            >
              Create
            </button>
            <button
              onClick={onClose}
              className="px-4 py-2 bg-gray-700 text-white rounded hover:bg-gray-600"
            >
              Cancel
            </button>
          </div>

        </div>
      </div>
    </>
  )
}